import React from 'react'
import Link from 'gatsby-link'

import ScreenshotPost from "../components/screenshot-post"
import ExpoPost from "../components/expo-post"


const Projects = () => (
  <div>
    <h2>Projects</h2>
    <ScreenshotPost/>
    <ExpoPost/>
    <Link to="/">Go back to the homepage</Link>
  </div>
)

export default Projects



